@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
  font-family: "Roboto", sans-serif;
}
body {
  background: linear-gradient(to right, #0da8f8, #05fc71);
}
#root {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.container {
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
}

.calculator {
  font-size: 2rem;
  width: 80%;
  max-width: 300px;
  border-radius: 5px;
  overflow: hidden;
}
.top-display {
  background-color: #023036;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  padding: 2rem 1.5rem;
  word-wrap: break-word;
  word-break: break-all;
  min-height: 8.1rem;
}
.previous-display {
  font-size: 1.4rem;
}
.buttons {
  display: grid;
  grid-gap: 2px;
  grid-template-columns: repeat(4, 1fr);
  padding: 2px;
  background-color: #fff;
}
.calculator button {
  border: none;
  background-color: #c0edf8;
  font-size: inherit;
  cursor: pointer;
  text-align: center;
  padding: 0.5em;
}
.span-2 {
  grid-column: span 2;
}

